@import url(https://fonts.googleapis.com/css2?family=Philosopher&family=Roboto&display=swap);
:root {
  --purple-color: #2e052f;
  --light-gold-color: #ead5b6;
  --gold-color: #d7b05a;
  --orange-color: #bb470e;
  --black: #111
}

.color-theme {
  --background: var(--purple-color);
  --primary-font-color: var(--light-gold-color);
  --secondary-font-color: var(--gold-color);
  --accent-color: var(--orange-color)
}
body {
  margin: 0;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  background-color: lavender
}
h1, h2, h3, h4 {
  font-family: 'Philosopher', sans-serif;
  color: var(--secondary-font-color);
}

p {
  font-family: 'Roboto', sans-serif;
  color: var(--background);
}

header {
  background-color: var(--background);
  color: var(--secondary-font-color);
  font-family: 'Philosopher', sans-serif;
}
.nav-item a {
  color: var(--primary-font-color);
  font-family: 'Roboto', sans-serif;
}
.nav-item a:hover {
  color: var(--accent-color);
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.form-label {
  text-align: left;
}


.card-container {
  display: flex;
  justify-content: center;
}

.card {
  margin: 50px;
  font-family: 'Roboto', sans-serif;
}

.btn-light a:hover {
  color: var(--accent-color);
}

#contact {
  background-color: var(--background);
  color: var(--secondary-font-color);
  font-family: 'Philosopher', sans-serif;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}

#contact ul {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  justify-content: center;
  
}
#contact li a{
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  color: var(--primary-font-color);
  font-family: 'Roboto', sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

